import axios from 'axios';
import React, { useCallback, useState, useEffect } from 'react';
import styled, { css } from 'styled-components';
import NewsItem from './NewsItem';
import { AiOutlineLoading3Quarters } from "react-icons/ai";

const NewsBlock = styled.div`
    border-top: 96px solid #ff385c;
    .loadWrap {
        display: flex; justify-content: center; align-items: center; min-height: 80vh;
        .loadicon {
            font-size: 80px; color: #333;
            animation: loadSpin infinite 5s linear;
        }
        @keyframes loadSpin {
            from {transform: rotate(0)}
            to {transform: rotate(3turn)}
        }
    }
    .row {
        .category {
            display: flex; justify-content: center; 
        }
        .articles {
            display: flex; flex-wrap: wrap; margin-top: 20px; margin-bottom: 10px;
        }
    }
`;

const Categ = styled.div`
    margin: 40px 8px 20px; padding: 10px 18px; font-size: 15px;
    background: #eee; border: 1px solid #eee; border-radius: 20px;
    color: #666;  transition: all 0.5s;
    &:hover {cursor: pointer; background: #333; color: #fff;}
    ${
        props => props.active && css`      
            background: #333; color: #fff;
        `     
    }
`;

const categories = [
    {name: "all", text: "전체보기" },
    {name: "business", text: "비즈니스" },
    {name: "entertainment", text: "엔터테인컨트" },
    {name: "sports", text: "스포츠" },
    {name: "health", text: "건강" },
    {name: "science", text: "과학" },
    {name: "technology", text: "기술" },
]
const Newssection = () => {

    const [category, setCategory] = useState('all')
    const onChange = useCallback(
        (name)=> {     // 가져온 name값으로 카테고리를 수정한다.
            setCategory(name)
        }
    , [])

    // response 데이터들을 담을 변수가 필요. 카테고리에 따라.
    const [articles, setArticles] =useState(null); // 처음 값
    const [loading, setLoading] = useState(false);  // false이면 아이콘 띄우기.

    const getNews = async () => {
        const query = category === 'all' ? '' : `&category=${category}`;
        const response = await axios.get(`https://newsapi.org/v2/top-headlines?country=kr${query}&apiKey=e114f8c65b374ce59e82e521cb5cef6c`)
        console.log(response)
        setArticles(response.data.articles)  // data안에 있는 articles키가 가지고 있는 배열을 가져오라.
        setLoading(true);
    }
     
    useEffect(
        () => {
            getNews()
        }, [category])  //카테고리라는 state 변수가 바뀌면 그에 따라 값을 가져와야함. (category값에 의존적으로)

    // loading이 false면 아이콘, true면 return문이 보이도록.(기본값이 false일때는 false문부터 쓰는게 좋음. 그래서 앞에 ! 붙임.)
    if (!loading) {
        // false이면,
        return (
            <NewsBlock>
                <div className='loadWrap'>
                    <AiOutlineLoading3Quarters className='loadicon' />
                </div>
            </NewsBlock>
        );
    } else {
        // true이면,
        return (
            <NewsBlock>
                <div className='row'>
                    <div className='category'>
                        {
                            categories.map((data, index) => <Categ active={category === data.name} key={index} onClick={()=> onChange(data.name)}>{data.text}</Categ>)
                        }
                    </div>
                    <div className='articles'>
                        {
                            // 초기 null일때는 이 자체를 하지 않게. (articles변수가 true일때만 진행하도록.)
                            articles && articles.map( (article, index)=> <NewsItem key={index} article={article} />)
                        }
                    </div>
                <p style={{color: '#ccc', fontSize:'14px', textAlign:'right', marginBottom: '20px'}}>이 페이지는 한국 기사 api 구현을 위해 만들어진 페이지로,<br />airbnb와는 무관한 페이지입니다.</p>
                </div>
            </NewsBlock>
        );
    }
};

export default Newssection;