import React from 'react';
import styled from 'styled-components';
import { Link } from 'react-router-dom';

const NewsItemBlock = styled.div`
    display: flex; margin-bottom: 30px; border: 1px solid #fff; width: 100%;
    transition: all 0.5s;
    &:hover {border: 1px solid #ccc; border-radius: 20px}
    .thumbnail {
        width: 35%; 
        img {border-radius: 20px; height: 250px; cursor: pointer;}
    }
    .contents {
        width: 65%; padding: 0 20px; cursor: default;
        p {color: #444}
    }
`;

const NewsItem = ({article}) => {
    const {title, description, urlToImage} = article;
    return (
        <NewsItemBlock>
            {
                urlToImage && (
                    <div className='thumbnail'>
                        <Link to={{pathname: '/news/detail', state: {title, urlToImage, description}}}><img src={urlToImage} alt={title} /></Link>
                    </div>
                )
            }
            {
                description && (
                    <div className='contents'>
                        <h2>{title}</h2>
                        <p>{description.slice(0,130)}...</p>
                    </div>
                )
            }
        </NewsItemBlock>
    );
};

export default NewsItem;