import React from 'react';
import Slider from 'react-slick';
import styled from 'styled-components';
import { IoIosArrowBack, IoIosArrowForward } from 'react-icons/io'

const SpaceItemBlock = styled.div`
    width: 31%; padding: 0 5px;
.ctbox {
    margin: 0px 0px 60px;  
    border: 1px solid #efefef; border-radius: 20px;
    &:hover .slidebox .slick-arrow {opacity: 1; background: #fff; color: #000; cursor: pointer;}
    .slidebox {
        margin: 0 ; position: relative; 
        .slide {
            height: 100% !important; 
            img {border-radius: 20px 20px 0 0; border: none}
        }
        .slick-arrow {
                position: absolute; top: 50%; transform: translateY(-50%);
                background: none; padding: 5px; z-index: 99999; width: 20px; 
                height: 20px; opacity: 0; transition: all 0.5s; border-radius: 50%; 
        }
        .slick-prev {left: 7px;}
        .slick-next {right: 7px;}
        .slick-dots {
            position: absolute; top: 85%; left: 50%; 
            transform: translate(-65%);
            li {
                button {width: 1px; height: 4px; text-indent: -999999px; 
                        background: rgba(255, 255, 255, 0.5); overflow: hidden;}
                &.slick-active button {background: #fff;}
            }
        }
    }
    .ctrow {
        display: flex; justify-content: space-between; 
        padding: 0px 15px; line-height: 0;
    }
    .ctfst {
        padding-top: 6px;
         p {
             &:nth-child(1) {font-weight: 700}
        }
    }
    .ctsed {
        padding-bottom: 6px;
        p {
            &:nth-child(1) {color: #aaa}
            &:nth-child(2) {font-size: 15px; color: #bbb}
        }
   }
}
`;


const SpaceItem = ({location, price, kilom, day, sdst, sdnd, sdrd, sdth}) => {
    const minisettings = {
        dots: true,
        infinite: true,
        autoplay: false,
        slidesToShow: 1,
        slidesToScroll: 1,
        arrows: true,
        prevArrow: <IoIosArrowBack />,
        nextArrow: <IoIosArrowForward />
    }

    return (
        <SpaceItemBlock>
            <div className='ctbox'>
                <Slider {...minisettings} className='slidebox'>
                    <div className='slide slide1'>
                        <img src={sdst} alt=''></img>
                    </div>
                    <div className='slide slide2'>
                        <img src={sdnd} alt=''></img>
                    </div>
                    <div className='slide slide3'>
                        <img src={sdrd} alt=''></img>
                    </div>
                    <div className='slide slide4'>
                        <img src={sdth} alt=''></img>
                    </div>
                </Slider>
                <div className='ctrow ctfst'>
                    <p>{location}</p>
                    <p>{price}</p>
                </div>
                <div className='ctrow ctsed'>
                    <p>{kilom}</p>
                    <p>{day}</p>
                </div>
            </div>
        </SpaceItemBlock>
    );
};

export default SpaceItem;