import React, { useCallback, useState } from 'react';
import styled, {css} from 'styled-components';
import 'slick-carousel/slick/slick.css';
import space from './space.json';
import SpaceItem from './SpaceItem'

const SpaceBlock = styled.div`
    border-top: 96px solid #ff385c;
    .tabs {display: flex; justify-content: start}
    .content {
        .cbox {display: flex; justify-content: space-between; 
               flex-wrap: wrap; overflow: hidden;}
        }
`;

const List = styled.div`
    margin: 40px 8px 20px; padding: 8px 10px 3px; font-size: 15px;
    color: #666; 
    &:hover {border-bottom: 3px solid #ddd; cursor: pointer; }
    ${
        props => props.active && css`      
            border-bottom: 3px solid #000; color: #000;
        `     
    }
`;


const tabTit = [
    {id:0, title: "해변 근처", part: space.part1},
    {id:1, title: "주택", part: space.part2},
    {id:2, title: "캠핑카", part: space.part3},
    {id:3, title: "수영장", part: space.part4},
    {id:4, title: "전통 한옥", part: space.part5},
    {id:5, title: "농장", part: space.part6},
    {id:6, title: "개성넘치는 숙소", part: space.part7}
]
        // 부모한테 받은 값을 써야함. activeTab, onSelect
const Spacesection = () => {

    const [activeTab, setActiveTab] = useState(0);

    const onSelect = useCallback(
        (id) => {    // 매개변수에서 id 값을 받아서.
                setActiveTab(id)          // activeTab의 id를 바꾸는것.
            }, []
        )
    
    

    return (
        <SpaceBlock>
            <div className='row'>
                <div className='tabs'>
                {
                    tabTit.map((data) => <List key={data.id} active={activeTab===data.id} onClick={()=>{onSelect(data.id)}}>{data.title}</List>)
                }   
                </div>
                <div className='content'>
                    <div className='cbox'>
                        {
                            tabTit[activeTab].part.map( (data, index) => <SpaceItem key={index} location={data.location} price={data.price} kilom={data.kilom} day={data.day} sdst={data.sdst} sdnd={data.sdnd} sdrd={data.sdrd} sdth={data.sdth} /> )
                        }
                    </div>
                </div>
            </div>
        </SpaceBlock>
    );
};

export default Spacesection;